.footer{
    margin: 0;
    padding: 0 5% 0 5%;
    width: 90%;
    height: 200px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    color: aliceblue;
    background: linear-gradient(to bottom, #282c34, 44%, black);
}

.requisites{
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: column;
    justify-content:center;
    align-items: flex-start;
}

.text{
    margin: 0;
    padding: 0;
    text-align: left;
} 
.text:nth-child(2){
    margin-bottom: 10px;
}

.contacts{
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: column;
    justify-content:center;
    align-items: center;
}


.listElement{
    margin: 0;
    padding: 0;
    list-style: none;
    list-style-type: none;
}